<template lang="">
    <div id="joinContainer">
        <div>
                <div>
                    <h2>JOIN</h2>
                    <p>안녕하세요 Fooding입니다.</p>
                </div>
                
                
                    <form id="join-form" action="#" method="POST" enctype="multipart/form-data">
                        <div id="join">

                        <div>
                            <input id="user" v-model="user" name="user" type="user" placeholder="이메일" autofocus >
                            <input id="pass1" v-model="passwd" name="pass1" type="password" placeholder="비밀번호"><br>
                            <input id="pass2" v-model="passwd2" type="password" placeholder="비밀번호 확인">
                        </div>
                        <div>
                            <label for="name"><p>이름</p></label>
                            <input id="name" v-model="userName" name="name" type="text" placeholder="이름을 입력하세요.">
                        </div>
                        <div class="text-left margin-tb3">
                            <label><p>성별</p></label>
                            <input id="man" v-model="gender" name="gender" type="radio" value="man">
                            <label for="man">남자</label>
                            <br>
                            <input id="women" v-model="gender" name="gender" type="radio" value="women">
                            <label for="women">여자</label>
                        </div>
                        <div>
                            <label for="phone"><p>연락처</p></label>
                            <input id="phone" v-model="phone" name="phone" type="tel" placeholder="연락처">
                        </div>
                        <div class="margin-tb3">
                            <label for="address"><p>주소</p></label>
                            <input id="address" v-model="address" name="address" type="text" placeholder="주소"><br>
                            <input id="address_detaile" v-model="daddress" name="address_detaile" type="text" placeholder="상세주소">
                        </div>
                        
                            <input id="agree_term" name="agree_term" type="hidden">
                            <input id="agree_privacy" name="agree_privacy" type="hidden">
                            <input id="agree_sms" name="agree_sms" type="hidden">
                            <input id="agree_user" name="agree_user" type="hidden">
                       
                    </div>
                    <div>
                        <button type="button" @click="onJoin">가입하기</button>
                    </div>
                    </form>
                
            </div>
    </div>
</template>
<script>
export default {
    name:"join",
    data() {
        return{
            user:'',
            passwd1:'',
            passwd2:'',
            userName:'',
            gender:'',
            phone:'',
            address:'',
            daddress:''
        }
    },
    methods : {
        onJoin(){
        this.$store.dispatch('insertUser', {
            user : this.user,
            passwd : this.passwd,
            passwd2 : this.passwd2,
            userName : this.userName,
            gender : this.gender,
            phone : this.phone,
            address : this.address,
            daddress : this.daddress

        })
        alert('회원가입하셨습니다.')
        this.$router.push('/');
        }
    }
    
}
</script>
<style scoped>
h2 {
    margin-left: 4%;
    font-size: 2em;
}
h2 + p {
    margin-left: 2.5%;
}
#joinContainer{
    margin-top: 3%;
    margin-left: 40%;
}
#joinContainer h2 {
    padding-left: 12%;
}
#joinContainer h2 + p {
    padding-left: 9%;
}
#join {
    border: 5px solid goldenrod;
    display: inline-block;
    padding: 20px;
    padding: 3%;
    margin-top: 20px;
    padding-right: 4%;
}
#join input:not([name='gender']) {
    width: 100%;
    height: 30px;
    margin-bottom: 1%;
    padding-left: 3%;
}

#join div {
    margin-top: 2%;
}

#joinContainer button{
    margin-left: 13%;
    margin-top: 1%;
}

form div:last-child{
    margin-left: 3%;
}

button[type="button"]{
    font-size: 1.2em;
    width: 100px;
    border: 1px solid rgb(185, 185, 185);
}
</style>